import { Component, OnInit } from '@angular/core';
import { KittensService } from '../../services/kittens.service';
import { Breed } from '../../models/breed.interface';

@Component({
  selector: 'app-kittens',
  templateUrl: './kittens.component.html',
  styleUrls: ['./kittens.component.css']
})
export class KittensComponent implements OnInit {

  breeds: Breed[] = [];

  constructor(private kittensService: KittensService) { }

  ngOnInit(): void {
      this.kittensService.getAllBreeds().subscribe((breeds) => this.breeds = breeds.slice(0, 30));
  }

}
