import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Breed } from '../models/breed.interface';

@Injectable({
  providedIn: 'root'
})
export class KittensService {

  constructor(private http: HttpClient) { }
    
    getAllBreeds(): Observable<Breed[]>{
        return this.http.get<Breed[]>('https://api.thecatapi.com/v1/breeds');
    }
    
    getBreedById(id: string): Observable<Breed>{
        return this.http.get<Breed>('https://api.thecatapi.com/v1/breeds/search?q='+id);
    }
    
    getImage(imageID: string): Observable<any>{
        return this.http.get<string>('https://api.thecatapi.com/v1/images/'+imageID);
    }
}
