import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { KittensService } from '../../services/kittens.service';
import { Breed } from '../../models/breed.interface';

@Component({
  selector: 'app-kitten',
  templateUrl: './kitten.component.html',
  styleUrls: ['./kitten.component.css']
})
export class KittenComponent implements OnInit {

  breed: Breed;
  catImg: string;
    
  constructor(private kittensService: KittensService, private activatedRoute: ActivatedRoute, private router: Router) { }

  ngOnInit(): void {
      const identifier = this.activatedRoute.snapshot.paramMap.get('id');
      this.kittensService.getBreedById(identifier).subscribe((breed)=>{
      if(!breed){
        return this.router.navigateByUrl('/');
      }
      this.breed = breed[0];     
      this.kittensService.getImage(breed[0].reference_image_id).subscribe((img) => this.catImg = img.url);
      })
      
  }

}
